"use client";
import React, { useState } from "react"; 
import { BudgetProvider } from "./context/BudgetContext";
import BudgetForm from "./components/BudgetForm/BudgetForm";
import BudgetList from "./components/BudgetList/BudgetList";
import AboutPage from "./about/page";
import "./App.css";

function App() {
  const [visibleComponent, setVisibleComponent] = useState<string>("");

  const toggleComponent = (componentName: string): void => {
    setVisibleComponent((prev) => (prev === componentName ? "" : componentName));
  };

  return (
    <BudgetProvider>
      <main className="main-container">
        <h1 className="title">Kişisel Bütçe Uygulaması</h1>
        <div className="space-y-4">
          <button
            className="button"
            onClick={() => toggleComponent("BudgetForm")}
          >
            {visibleComponent === "BudgetForm" ? "Formu Gizle" : "Formu Göster"}
          </button>
          {visibleComponent === "BudgetForm" && <BudgetForm />}

          <button
            className="button"
            onClick={() => toggleComponent("BudgetList")}
          >
            {visibleComponent === "BudgetList" ? "Listeyi Gizle" : "Listeyi Göster"}
          </button>
          {visibleComponent === "BudgetList" && <BudgetList />}

          <button
            className="button"
            onClick={() => toggleComponent("AboutPage")}
          >
            {visibleComponent === "AboutPage" ? "Hakkında Gizle" : "Hakkında Göster"}
          </button>
          {visibleComponent === "AboutPage" && <AboutPage />}
        </div>
      </main>
    </BudgetProvider>
  );
}

export default App;
