"use client";

import { useState } from "react";
import { useBudget } from "../../context/BudgetContext";
import styles from './BudgetForm.module.css';

export default function BudgetForm() {
  const { dispatch } = useBudget();
  const [type, setType] = useState<"income" | "expense">("income");
  const [category, setCategory] = useState("");
  const [description, setDescription] = useState("");
  const [amount, setAmount] = useState("");
  const [date, setDate] = useState("");
  const [budgetLimit, setBudgetLimit] = useState(""); // Yeni state

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const newTransaction = {
      id: Date.now().toString(),
      type,
      category,
      description,
      amount: parseFloat(amount),
      date,
    };

    // Dispatch işlemi
    dispatch({
      type: "ADD_TRANSACTION",
      payload: newTransaction,
    });

    // LocalStorage'a kaydetme işlemi
    const existingTransactions = JSON.parse(localStorage.getItem("transactions") || "[]");
    localStorage.setItem("transactions", JSON.stringify([...existingTransactions, newTransaction]));

    // Formu temizleme
    setCategory("");
    setDescription("");
    setAmount("");
    setDate("");
  };

  const handleBudgetLimitChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setBudgetLimit(e.target.value);
    localStorage.setItem("budgetLimit", e.target.value); // Bütçe limiti localStorage'a kaydediliyor
  };

  return (
    <form onSubmit={handleSubmit} className={styles['form-container']}>
      <div>
        <label>Tip:</label>
        <select value={type} onChange={(e) => setType(e.target.value as "income" | "expense")}>
          <option value="income">Gelir</option>
          <option value="expense">Gider</option>
        </select>
      </div>
      <div>
        <label>Kategori:</label>
        <input
          type="text"
          value={category}
          onChange={(e) => setCategory(e.target.value)}
          required
        />
      </div>
      <div>
        <label>Açıklama:</label>
        <input
          type="text"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          required
        />
      </div>
      <div>
        <label>Tutar:</label>
        <input
          type="number"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}
          required
        />
      </div>
      <div>
        <label>Tarih:</label>
        <input
          type="date"
          value={date}
          onChange={(e) => setDate(e.target.value)}
          required
        />
      </div>
      <div>
        <label>Bütçe Limiti:</label>
        <input
          type="number"
          value={budgetLimit}
          onChange={handleBudgetLimitChange}
          placeholder="Örn: 5000"
        />
      </div>
      <button type="submit" className="bg-blue-500 text-white px-4 py-2">
        Ekle
      </button>
    </form>
  );
}