"use client";

import { useEffect, useState } from "react";
import styles from './BudgetList.module.css';

interface Transaction {
  id: string;
  type: "income" | "expense";
  category: string;
  description: string;
  amount: number;
  date: string;
}

export default function BudgetList() {
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [budgetLimit, setBudgetLimit] = useState<number | null>(null);

  useEffect(() => {
    // LocalStorage'dan işlemleri al
    const storedTransactions = JSON.parse(localStorage.getItem("transactions") || "[]");
    setTransactions(storedTransactions);

    // LocalStorage'dan bütçe limitini al
    const storedBudgetLimit = localStorage.getItem("budgetLimit");
    setBudgetLimit(storedBudgetLimit ? parseFloat(storedBudgetLimit) : null);
  }, []);

  return (
    <div className={styles['list-container']}>
      <h1 className={styles['list-title']}>Bütçe Listesi</h1>

      {/* Bütçe limiti gösterimi */}
      <div className={styles['budget-limit']}>
        <strong>Bütçe Limiti:</strong> {budgetLimit !== null ? `${budgetLimit.toFixed(2)} TL` : "Belirtilmedi"}
      </div>

      {transactions.length === 0 ? (
        <p className={styles['empty-message']}>Henüz eklenmiş bir işlem yok.</p>
      ) : (
        <table className={styles['table']}>
          <thead>
            <tr>
              <th>Tip</th>
              <th>Kategori</th>
              <th>Açıklama</th>
              <th>Tutar</th>
              <th>Tarih</th>
            </tr>
          </thead>
          <tbody>
            {transactions.map((transaction) => (
              <tr key={transaction.id}>
                <td>{transaction.type === "income" ? "Gelir" : "Gider"}</td>
                <td>{transaction.category}</td>
                <td>{transaction.description}</td>
                <td>{transaction.amount.toFixed(2)} TL</td>
                <td>{transaction.date}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}